<template>
  <!--
  el-form表单组件
    -:model="ruleForm"  和表单绑定的数据对象(fileForm)
    -:rules="rules" 校验规则
    - ref="ruleForm"  可以通过refs获取当前这个组件
    - label-width="150px"  就是el-form-item label标签的宽度
  el-form-item表单元素组件
    -label：标签名称
    -prop="name"校验/重置用的
  -->
  <el-row type="flex" justify="center" align="middle">
    <el-col :span="8">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="用户名" prop="username">
          <el-input v-model="ruleForm.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="ruleForm.password"></el-input>
        </el-form-item>
        <el-form-item>
          <!--
          primary:蓝色
          success:绿色
          danger:红色
          -->
          <el-button type="success" @click="startLogin">登陆</el-button>
          <el-button @click="resetForm">重置</el-button>
        </el-form-item>
      </el-form>
      <!-- <h1>我是login页的h1</h1> -->
    </el-col>
  </el-row>
</template>
<script>
/* eslint-disable */
import axios from "axios";

export default {
  data() {
    return {
      ruleForm: {
        username: "",
        password: ""
      },
      // 校验规则---prop=""
      rules: {
        // 用户名
        username: [
          // 判断是否需要输入
          // required：必须
          { required: true, message: "请输入用户名", trigger: "blur" },
          // 判断输入格式是否正确
          { min: 3, max: 5, message: "长度在 3 到 5 个字符", trigger: "blur" }
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          { min: 5, max: 10, message: "长度在 5 到 10 个字符", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    // 开始登陆
     startLogin() {
      // 校验：
      this.$refs.ruleForm.validate(async valid => {
        console.log(valid);
        if (!valid) {
          return alert("格式不正确");
        }
        let res=await axios.post("http://localhost:8888/api/private/v1/login",this.ruleForm)
        if (res.data.meta.status === 200) {
              // 0. 把token保存到本地
              console.log(res.data);
              localStorage.setItem("token", res.data.data.token);
              // 1. 如果正确，则弹窗提示登陆成功
              this.$message({
                message: res.data.meta.msg,
                type: "success",
                duration: 800
              });
              // 2. 跳转到home页
              this.$router.push("./home");

            } else {
              this.$message({
                message: res.data.meta.msg,
                type: "error",
                duration: 800
              });
            }
        // 就可以放心的发送请求
        // axios
        //   .post("http://localhost:8888/api/private/v1/login", this.ruleForm)
        //   .then(res => {
        //     console.log(res);
        //     if (res.data.meta.status === 200) {
        //       // 0. 把token保存到本地
        //       console.log(res.data);
        //       localStorage.setItem("token", res.data.data.token);
        //       // 1. 如果正确，则弹窗提示登陆成功
        //       this.$message({
        //         message: res.data.meta.msg,
        //         type: "success",
        //         duration: 800
        //       });
        //       // 2. 跳转到home页
        //       this.$router.push("./home");
        //     } else {
        //       this.$message({
        //         message: res.data.meta.msg,
        //         type: "error",
        //         duration: 800
        //       });
        //     }
        //   });

      });
    },
    resetForm() {
      // 重置 恢复到初始的值
      this.$refs.ruleForm.resetFields();
    }
  }
};
</script>
<style scoped>
.el-row {
  height: 100%;
  background-color: #2d434c;
}
.el-col {
  background-color: white;
  padding: 30px 30px;
  border-radius: 15px;
}
h1 {
  color: red;
}
</style>



